const login = require('../../static/utils/utils').Login;
const query = require('../../static/utils/utils').Query;
const Login = new login();
const Query = new query();

var _this;
export default {
	data() {
		return {
		   userInfo:"",
		   projectId:"",
		   sprintId:"",
		   departmentId:""
		}
	},
	onShow(){
		_this = this;
		uni.getStorage({
			key:"userInfo",
			success:(res)=>{
				let id = {
					id:res.data.id
				}
			  Query.findUser(id)
			  .then(data=>{     
				  console.log("查询到的用户信息",data.data.records);
				  _this.userInfo = data.data.records;
			  })
			  .catch(Error=>{
				  uni.showToast({
				  	title:'网络错误',
					duration:5000,
					icon:"none"
				  })
			  })
			},
			fail:()=>{
				uni.redirectTo({
					url:'../login/login'
				})
			}
		})
	},
	
	methods: {
		
	}
}
